body {
  letter-spacing: .02em;
  color: var(--default-color);
  background-color: var(--white);
  flex-direction: column;
  font-family: Poppins, sans-serif;
  font-size: 15px;
  font-weight: 500;
  line-height: 1.67;
  display: flex;
}

.main {
  flex-grow: 1;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.nav-menu__btn {
  z-index: 1;
  cursor: pointer;
  transition: opacity var(--transit);
  border: none;
  justify-content: center;
  align-items: center;
  height: 42px;
  margin-right: 20px;
  display: none;
  position: relative;
}

@media (max-width: 575px) {
  .nav-menu__btn {
    margin-right: 15px;
  }
}

.nav-menu__btn:hover, .nav-menu__btn:focus, .nav-menu__btn:active {
  opacity: .5;
}

@media (max-width: 980px) {
  .nav-menu__btn {
    display: flex;
  }
}

.nav-menu__btn:before, .nav-menu__btn:after {
  content: "";
  transition: all var(--transit);
  background-color: #e7e7e7;
  width: 36px;
  height: 2px;
  position: absolute;
  left: 0;
  right: 0;
  transform: rotate(180deg);
}

.nav-menu__btn span {
  transition: all var(--transit);
  background-color: #e7e7e7;
  width: 36px;
  height: 2px;
}

.nav-menu__btn:before {
  top: 7px;
}

.nav-menu__btn:after {
  bottom: 7px;
}

.nav-menu__btn.active:before {
  width: 15px;
  top: 15px;
  transform: rotate3d(0, 0, 1, -35deg);
}

.nav-menu__btn.active:after {
  width: 15px;
  bottom: 15px;
  transform: rotate3d(0, 0, 1, 35deg);
}

.nav-menu__btn.active span {
  transform: rotate(180deg);
}

.logo {
  color: var(--white);
  transition: opacity var(--transit);
  align-items: center;
  font-family: Montserrat, sans-serif;
  font-size: clamp(23px, 6vw, 40px);
  font-weight: 600;
  display: flex;
}

.logo-link {
  flex-shrink: 0;
  align-items: center;
  display: flex;
}

.logo-img {
  transition: width var(--transit);
  width: clamp(35px, 4vw, 60px);
  margin-right: 10px;
}

.logo:hover, .logo:focus, .logo:active {
  opacity: .5;
}

.animated-element {
  opacity: 0;
  visibility: hidden;
  transition: opacity var(--transit), transform var(--transit);
  overflow: hidden;
}

.animated {
  opacity: 1;
  visibility: visible;
  animation-fill-mode: both;
  overflow: hidden;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.no-scroll {
  overflow: hidden;
}

.hidden {
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.scrollUpButton {
  z-index: 20;
  background-color: var(--hover-purple);
  color: var(--white);
  cursor: pointer;
  transition: transform var(--transit), opacity var(--transit);
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  display: flex;
  position: fixed;
  bottom: 40px;
  right: 40px;
  transform: translate3d(0, 100px, 0);
}

@media (max-width: 575px) {
  .scrollUpButton {
    bottom: 15px;
    right: 15px;
  }
}

.scrollUpButton:hover, .scrollUpButton:focus, .scrollUpButton:active {
  opacity: .6;
}

.scrollUpButton .scrollUpIcon {
  fill: var(--white);
  width: 16px;
  height: 14px;
}

.social-links {
  transition: transform var(--transit), opacity var(--transit);
  column-gap: 15px;
  display: flex;
}

@media (max-width: 980px) {
  .social-links {
    background-color: var(--default-bg);
    opacity: 0;
    z-index: 10;
    border-radius: 0 0 0 5px;
    flex-direction: column;
    row-gap: 10px;
    padding: 20px 8px;
    position: fixed;
    top: 74px;
    right: 0;
    transform: translateY(-135%);
    box-shadow: -4px 7px 10px 2px #0000002b;
  }
}

.social-links.active {
  opacity: 1;
  transform: translateY(0);
}

.social-item {
  justify-content: center;
  align-items: center;
  height: 35px;
  display: flex;
}

.social-link {
  height: 100%;
  padding: 5px;
}

@media (max-width: 980px) {
  .social-link {
    padding: 5px 15px;
  }
}

.social-icon {
  transition: fill var(--transit);
  fill: #ffffff80;
}

.social-icon:hover {
  fill: var(--white);
}

.social-button {
  cursor: pointer;
  transition: opacity var(--transit);
  min-width: 42px;
  height: 42px;
  margin-left: auto;
  display: none;
  position: relative;
}

@media (max-width: 980px) {
  .social-button {
    display: block;
  }
}

.social-button:hover, .social-button:focus, .social-button:active {
  opacity: .5;
}

.social-button span, .social-button:before, .social-button:after {
  background-color: var(--white);
  transition: all var(--transit);
  border-radius: 50%;
  width: 6px;
  height: 6px;
  position: absolute;
}

.social-button:before {
  content: "";
  top: 5px;
  left: 50%;
  transform: translateX(-50%);
}

.social-button:after {
  content: "";
  bottom: 5px;
  left: 50%;
  transform: translateX(-50%);
}

.social-button span {
  display: block;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.social-button.active span {
  transform: scale(.7)translate(-4px, -4px);
}

.social-button.active:before {
  transform: scale(.7)translate(-4px, 28px);
}

.social-button.active:after {
  transform: scale(.7)translate(-4px, -29px);
}

.header {
  transition: padding-top var(--transit), padding-bottom var(--transit), background-color var(--transit);
  align-items: center;
  min-height: 75px;
  padding: 35px 0;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

@media (max-width: 980px) {
  .header {
    background-color: var(--default-bg);
    border-bottom-color: var(--default-bg);
    z-index: 20;
    padding-top: 10px;
    padding-bottom: 10px;
    box-shadow: 0 0 15px #15151559;
  }
}

.header-sticky {
  background: var(--default-bg);
  border-bottom-color: var(--default-bg);
  z-index: 20;
  padding: 10px 0;
  box-shadow: 0 0 15px #15151559;
}

.header-sticky.logo {
  font-size: clamp(20px, 4vw, 25px);
}

.header-sticky.logo-img {
  width: clamp(30px, 4vw, 40px);
  margin-right: 10px;
}

.header body .header.no-scroll {
  padding-right: 17px;
}

.header-inner {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header-nav {
  align-items: center;
  width: 100%;
  display: flex;
}

.header-links-wrap {
  justify-content: space-between;
  align-items: center;
  margin-left: auto;
  display: flex;
}

.header-nav-links {
  transition: transform var(--transit);
  align-items: center;
  column-gap: 50px;
  margin-right: 30px;
  display: flex;
}

@media (max-width: 1200px) {
  .header-nav-links {
    column-gap: 18px;
  }
}

@media (max-width: 980px) {
  .header-nav-links {
    z-index: 8;
    background-color: var(--default-bg);
    border-right: 1px solid var(--default-bg);
    flex-direction: column;
    align-items: baseline;
    row-gap: 20px;
    width: 270px;
    height: 100%;
    padding: 20px 35px;
    position: fixed;
    top: 74px;
    left: 0;
    transform: translateX(-115%);
    box-shadow: 5px 10px 15px #15151559;
  }
}

.header-nav-links.active {
  transform: translateX(0);
}

.header-nav-item {
  display: flex;
}

.header-nav-link {
  letter-spacing: .02em;
  text-transform: uppercase;
  color: var(--white);
  transition: color var(--transit);
  flex-shrink: 0;
  font-size: 15px;
  font-weight: 500;
  line-height: 1.2;
  display: flex;
}

@media (max-width: 980px) {
  .header-nav-link {
    padding: 15px 0;
  }
}

.header-nav-link:hover, .header-nav-link:focus, .header-nav-link:active, .header-nav-link--active {
  color: var(--accent);
}

.header-nav-backdrop {
  z-index: 1;
  display: none;
  position: fixed;
  inset: 0;
}

.header-nav-backdrop.active {
  display: block;
}

.header-nav-item select {
  cursor: pointer;
  background-color: #0000;
  border: none;
  width: 128px;
  padding: 0;
}

.header-nav-item select option {
  background-color: var(--default-bg);
}

.header-nav-item select:focus {
  color: var(--white);
}

@media (max-width: 980px) {
  .header-nav-item select {
    background-color: var(--default-bg);
  }
}

.hero {
  background-image: linear-gradient(#000000a6, #000000a6), url("bg-ps-main.caac5bf3.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
}

.hero-inner {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.hero-title {
  text-transform: uppercase;
  color: var(--white);
  letter-spacing: .01em;
  text-shadow: 0 4px 12px #0000004d;
  margin-bottom: 40px;
  padding: 0 20px;
  font-family: Montserrat, sans-serif;
  font-size: clamp(27px, 7vw, 70px);
  font-weight: 900;
  line-height: 1.2;
  animation-delay: .3s;
}

@media (max-width: 575px) {
  .hero-title {
    padding: 0;
  }
}

.hero-subtitle {
  color: var(--white);
  letter-spacing: -.03em;
  padding: 0 20px;
  font-size: 18px;
  font-weight: 400;
  animation-delay: 1s;
}

@media (max-width: 575px) {
  .hero-subtitle {
    padding: 0;
  }
}

.appointment {
  background-color: var(--default-color);
  color: var(--white);
  padding: 60px 0;
}

.appointment-form {
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.appointment-form:after {
  content: "";
  opacity: 0;
  visibility: hidden;
  transition: opacity var(--transit);
  z-index: 100;
  background: #333333e6 url("loading.7d130c59.gif") center / 100px no-repeat;
  position: fixed;
  inset: 0;
}

.appointment-form._sending:after {
  opacity: 1;
  visibility: visible;
}

@media (max-width: 1200px) {
  .appointment-form {
    flex-direction: column;
    gap: 20px;
    max-width: 500px;
  }
}

.appointment-label {
  text-transform: uppercase;
  flex-direction: column;
  width: 100%;
  height: 80px;
  padding: 0 12px;
  font-weight: 400;
  display: flex;
}

.appointment-label_mistake {
  text-transform: none;
  color: #f5543f;
  letter-spacing: 0;
  margin-left: auto;
  padding-top: 2px;
  font-size: 9px;
  line-height: 1.33;
  display: none;
}

.appointment-label._error .appointment-label_mistake {
  display: block;
}

.appointment-input {
  color: var(--white);
  border: none;
  border-bottom: 1px solid #ffffff1a;
  outline: none;
  padding: 10px 16px 16px 0;
  font-size: 18px;
}

.appointment-input::placeholder {
  color: var(--white);
  font-size: 18px;
}

.appointment-input._error {
  border-color: #f5543f;
}

.appointment-submit-btn {
  background-color: var(--white);
  color: var(--default-color);
  text-transform: uppercase;
  letter-spacing: .002em;
  text-overflow: ellipsis;
  white-space: nowrap;
  transition: color var(--transit), background-color var(--transit), border-color var(--transit);
  border-color: #0000;
  width: 100%;
  max-width: 250px;
  margin-top: 12px;
  margin-left: 27px;
  padding: 18px 35px;
  font-size: 15px;
  font-weight: 600;
  line-height: 1.5;
  overflow: hidden;
}

@media (max-width: 1200px) {
  .appointment-submit-btn {
    margin-top: 45px;
    margin-left: 0;
  }
}

.appointment-submit-btn:hover, .appointment-submit-btn:focus, .appointment-submit-btn:active {
  color: var(--white);
  background-color: var(--hover-purple);
}

.air-datepicker-global-container .air-datepicker {
